import React from 'react'
import { BrowserRouter as Router, Route, NavLink } from 'react-router-dom'
import { TunesScreen, SetlistScreen, RandomScreen } from './components'
import { findIndex } from 'lodash'
import tunesJson from './tunes'
import './App.css'

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      tunes: tunesJson,
      setlist: [],
      random: {}
    }
  }

  componentDidMount() {
    // Get tunes from API
    // Uncomment in production
    // ----------------------------------------------------
    // fetch('https://thomas-mcevoy.com/api/tunes/')
    //   .then(response => JSON.parse(response))
    //   .then(responseJson => this.setState({
    //     tunes: responseJson
    //   }))
    //   .catch(error => console.log(error))

    this.getRandomTune()
  }

  getRandomTune() {
    const { tunes } = this.state
    this.setState((prevState, props) => {
      return {
        ...prevState,
        random: tunes[Math.floor(Math.random() * tunes.length)]
      }
    })
  }

  toggleSelected(tune) {
    this.setState((prevState, props) => {
      return {
        tunes: this.getNextTunes(tune, prevState.tunes),
        setlist: this.getNextSetlist(tune, prevState.setlist)
      }
    })
  }

  getNextTunes(tune, prevTunes) {
    let nextTunes = [...prevTunes]
    const index = findIndex(prevTunes, {title: tune.title})
    let updatedTune = Object.assign({}, prevTunes[index])
    updatedTune.selected = updatedTune.hasOwnProperty('selected') ? 
      !updatedTune.selected : true
    nextTunes[index] = updatedTune
    return nextTunes
  }

  getNextSetlist(tune, prevSetlist) {
    let nextSetlist = [...prevSetlist]
    const index = findIndex(prevSetlist, {title: tune.title})
    if (index === -1)
      nextSetlist.push(tune)
    else
      nextSetlist.splice(index, 1)
    return nextSetlist
  }

  render() {
    return (
      <Router>
        <div className="App">
          <header className="App-header">
            <div className="Router">
              <NavLink className="Router-link" to="/tunes">Tunes</NavLink>
              <NavLink className="Router-link" to="/setlist">Setlist</NavLink>
              <NavLink className="Router-link" to="/random">Random</NavLink>
            </div>
          </header>
          <main className="App-main">
            <Route path="/tunes" render={() =>
              <TunesScreen
                tunes={this.state.tunes}
                toggleSelected={this.toggleSelected.bind(this)}
              />
            }/>
            <Route path="/setlist" render={() =>
              <SetlistScreen
                setlist={this.state.setlist}
                toggleSelected={this.toggleSelected.bind(this)}
              />
            }/>
            <Route path="/random" render={() => 
              <RandomScreen
                random={this.state.random}
                getRandomTune={this.getRandomTune.bind(this)}
              />
            }/>
          </main>
        </div>
      </Router>
    )
  }
}

export default App



