import React from 'react'
import { Tune } from '.'
import '../App.css'

const TunesScreen = (props) => (
  <div className="Tunes-screen">
    <ul className="Tunes-list">
      {props.tunes.map(tune =>
        <Tune
          key={tune.title}
          tune={tune}
          toggleSelected={props.toggleSelected}
        />
      )}
    </ul>
  </div>
)

export default TunesScreen


